import React from 'react';
import { Col, Row } from 'reactstrap';
import Thumbnail from 'src/components/Thumbnail';
import { faSuitcase, faMapMarkerAlt } from '@fortawesome/free-solid-svg-icons';

import BasicInformation from './BasicInformarion';
import DashboardCard from 'src/components/DashboardCard';
import LocationMap from 'src/components/LocationMap';
import JobSummary from './JobSummary';
import SocialProfile from './SocialProfile';
import RecentActivity from './RecentActivity';

const Dashboard = (props) => {
    const data = [
        {
            thumbnailImg: props.user.picture,
            title: props.user.firstName,
            jobCategory: ' Software firm',
            jobCategoryIcon: faSuitcase,
            location: ' Los angeles',
            locationIcon: faMapMarkerAlt,
            buttonName: 'View profile',
            className: 'mb-4',
        },
    ];
    console.log(props.user);
    return (
        <>
            {data && data.map((item, i) => <Thumbnail key={i} {...item} user={props.user} />)}
            <Row className='mb-4'>
                <Col lg={5}>
                    <BasicInformation user={props.user} />
                    <SocialProfile />
                </Col>
                <Col lg={7}>
                    <DashboardCard title='Our location'>
                        <LocationMap height={260} />
                    </DashboardCard>
                    <JobSummary />
                </Col>
            </Row>
            <RecentActivity />
        </>
    );
};

export default Dashboard;
